.heading-primary {
  color: $color-white;
  text-transform: uppercase;
  font-family: "Megrim";
  // font-size: 8em;

  backface-visibility: hidden;
  margin-bottom: 3rem;

  &--main {
    display: block;
    font-size: 6rem;
    font-weight: 400;
    letter-spacing: 3.5rem;

    animation-name: moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;

    @include respond(tab-land) {
      letter-spacing: 1rem;
      // font-family: 5rem;
      font-size: 12rem;
    }
    /*
        animation-delay: 3s;
        animation-iteration-count: 3;
        */
  }

  &--sub {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.75rem;
    animation: moveInRight 1s ease-out;

    @include respond(tab-land) {
      letter-spacing: 0.5rem;
      font-size: 9rem;
    }
  }
}

.heading-secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  background-image: linear-gradient(
    to right,
    $color-primary-light,
    $color-primary-dark
  );
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.2rem;
  transition: all 0.2s;

  @include respond(tab-port) {
    font-size: 3rem;
  }

  @include respond(phone) {
    font-size: 2.5rem;
  }

  &:hover {
    transform: skewY(2deg) skewX(15deg) scale(1.1);
    text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
  }

  @include respond(tab-land) {
    font-size: 6rem;
  }
}

.heading-tertiary {
  font-size: $default-font-size;
  font-weight: 700;
  text-transform: uppercase;

  @include respond(tab-land) {
    font-size: 4rem;
  }
}

.paragraph {
  font-size: $default-font-size;
  text-align: justify;
  margin: 1em;

  &:not(:last-child) {
    margin-bottom: 3rem;
  }
}

.paragraph-place {
  font-size: $default-font-size;
  text-align: center;
  margin: 1em;

  &:not(:last-child) {
    margin-bottom: 3rem;
  }
}

